.movies_list_bg {
  background-color: lavenderblush;
  color: black;
  font-size: 12px;
  width: 100%;
  height: 100%;
  background-size: 100% 100%;
}
.movies_list {
  height: auto;
  width: 230px;
  padding: 5px;
  margin: 3em;
  display: inline-block;
}
.movies_list h2 {
  float: left;
  display: block;
  width: 100%;
  margin: 5px 0px;
}
.movies_list a {
  color: black;
  text-decoration: none;
}
.movies_list span {
  float: left;
  font-size: 15px;
  width: 100%;
}
.movies_list img {
  width: 100%;
}
